@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&family=Hind+Madurai:wght@400;500;600;700&family=Roboto:wght@400;500;700&display=swap');

* {
	--white-color: #ffffff;
	--black-color: #000000;
	--black-150: #150101;

	--accent-color: #d23166;

	--hover-time: .4s;

	--font-roboto: 'Roboto', sans-serif;
	--font-hind-madurai: 'Hind Madurai', sans-serif;
	--font-fredoka: 'Fredoka One', cursive;
}

body {
	font-family: var(--font-roboto);
	font-size: 16px;
	font-weight: normal;
	line-height: 22px;
	color: var(--black-color);
}

.container {
	padding: 0 15px;
	max-width: 1155px;
	margin: 0 auto;
}

.app {
	max-width: 1600px;
	margin: 0 auto;
}